@import "../../../mainapp/color";

::placeholder {
  font: normal normal normal 16px/19px Muli;
  color: $placeholderAndBorder
}

input[type='text'] {
  font: normal normal normal 16px/25px Muli;
}

input[type="radio"] {
  /* remove standard background appearance */
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  /* create custom radiobutton appearance */
  display: inline-block;
  width: 24px;
  height: 24px;
  padding: 5px;
  /* background-color only for content */
  background-clip: content-box;
  border: 2px solid $blue;
  // background-color: $blue;
  border-radius: 50%;
}

/* appearance for checked radiobutton */
input[type="radio"]:checked {
  background-color: $blue;
}


.shipping__info__container {
  font: normal normal 600 16px/24px Muli;
}

.guest__checkout__div {
  margin-bottom: 20px;
  font: normal normal 600 36px/30px Source Serif Pro;
}

.contract__information__div {
  font: normal normal 600 20px/25px Muli;
  margin-bottom: 5px;
}

.contract__information_sub_title {
  font: normal normal normal 16px/25px Muli;
  margin-bottom: 16px;
}

.checkout__methods__div {
  font: normal normal 600 20px/25px Muli;
  margin-bottom: 24px;
  border-top: $PayementAndShipmentBorder 2px solid;
  padding-top: 24px;
}

.checkout__methods__div__toggel {
  font: normal normal 600 20px/25px Muli;
  margin-bottom: 24px;
  border-top: none;
  padding-top: 24px;
}

.input__box {
  width: -webkit-fill-available;
  margin-right: 33px;
  height: 44px;
  border: 2px solid #B5B7BC;
  border-radius: 8px;
}

.input__box__select {
  height: 52px;
}

.checkout__step1__btn {
  width: 347px;
  height: 40px;
  margin-top: 41px;
  margin-left: 10vw;
  margin-bottom: 48px;
  background: var(--white) 0% 0% no-repeat padding-box;
  background: $white 0% 0% no-repeat padding-box;
  border: 2px solid $buttonOutline;
  border-radius: 10px;
  color: $blue;
  opacity: 1;
}

.shipping__method__summary__div {
  font: normal normal 600 16px/20px Muli;
  padding-top: 32px;
  padding-bottom: 16px;
  padding-left: 32px;
}

.checkout__info__show {
  display: block;
}

.checkout__info__hide {
  display: none;
}

.label__name__div {
  padding-bottom: 8px;
}

.input__box__div {
  padding-bottom: 25px;
}

.shipping__info__summary__container {
  width: -webkit-fill-available;
  height: max-content;
  border: $PayementAndShipmentBorder 2px solid;
  margin-bottom: 16px;
}

.btn__span {
  font: normal normal bold 14px/16px Muli;
}

.shipping__method__summary__label__div {
  font: normal normal normal 16px/24px Muli;
  letter-spacing: 0px;
  color: #535965;
  margin-left: 32px;
  margin-bottom: 32px;
}

.checkout__edit__label {
  font: normal normal 600 16px/22px Muli;
  letter-spacing: 0px;
  color: $blue;
  opacity: 1;
  margin-top: -3px;
}

.checkout__edit__icon {
  width: 16px;
  height: 16px;
}

.options {
  font: normal normal normal 16px/19px Muli;
}

.optional__field__span {
  font: normal normal normal 14px/16px Muli;
  letter-spacing: 0px;
  color: $regularLable;
  // margin-left: 9vw;
}

.checkout__methods__accordian__label {
  color: $checkoutAccordian;
}

//mobile View
@media (min-width: 320px) and (max-width: 768px) {
  .checkout__step1__btn {
    width: -webkit-fill-available;
    margin-left: 5vw;
    margin-right: 5vw;
  }

  .input__box {
    margin-right: 0px;
  }
}